        // remove the CSS
        removeCSS();

        // restore the container's initial content
        unmount();
        container.innerHTML = '';
        containerChildren.forEach(function (child) {
            container.appendChild(child);
        });

        // destroy the spy
        if (spy) {
            spy.destroy();
        }
